.experienceContainer {
  width: 60rem;
  height: 400rem;
  margin: auto;
}

.employmentContainer {
  position: relative;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.employerCompany {
  font-family: "Quicksand", Arial, sans-serif;

  font-weight: 500;
}

.employmentRow {
  display: block;
  margin: 5rem;
  padding: 1rem;
  overflow: visible;
  background: red;
  width: 85%;
  height: 37.5rem;
}

