.projectsContainer {
  position: relative;
  width: 100%;
  height: 100%;
  margin: auto;
  margin-top: 5rem;
  margin-bottom: 5rem;
}

.projectRow {
  width: 90%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack:justify;
      justify-content:space-between;
  margin: auto;
  margin-top: 2rem;
}

.project {
  width: 40rem;
  height: 30rem;
  background: #F1F3F2;
}

.projectImage {
  height: 30rem;
  width: 40rem;
  background: #F1F3F2;
}

.mobileProject {
  height: 30rem;
  width: 40rem;
  background: white;
  background: #F1F3F2;
}

.mobileProjectImage {
  height: 25rem;
  width: 12rem;
  margin: auto;
  margin-top: 2.5rem;
}

.project_img__description {
  position: absolute;
  background: rgba(29, 106, 154, 0.72);
  color: #fff;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity 2s, visibility 2s;
  -o-transition: opacity 2s, visibility 2s;
  transition: opacity 2s, visibility 2s;
}

.imageDirections {
  font-size: 1.2rem;
  font-family: 'Montserrat', sans-serif;
  color: #007bb6;
}

#project_img_bottom_description {
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}

#project_img_top_description {
  left: 0;
  right: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

#project_img_left_description {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

#project_img_right_description {
  top: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.mobileProjectRow {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack:distribute;
      justify-content:space-around;
  -ms-flex: 1 1;
      flex: 1 1;
  margin: auto;
  margin-left: 10rem;

  .mobileProjectDiv {
    width: 10rem;
    height: 10rem;
    background: red;
    -ms-flex: 1 1;
        flex: 1 1;
  }
}

.noogleContainer {
  width: 40rem;
  height: 20rem;
  margin: auto;
}

#projectNoogle {
  display: block;
  margin: auto;
  width: 400px;
  height: 250px;
}

 .githubProjectsDiv {
   width: 80rem;
   height: 50rem;
 }

#OneDentalMatch {
  width: 13rem;
  height: 22rem;
  border: 3px solid white;
  border: 3px solid #282c34;
}

#projectsBackground {
  background: rgb(73, 110, 178);
  background: #282c34;
  color: white;
  padding: 1rem;
}

.projectsDiv:hover .img__description {
  visibility: visible;
  opacity: 1;
  width: 100%;
  height: 100%;
}

#projectsTitleContainer {
  width: 50rem;
  height: 10rem;
  margin: auto;
}

#portfolioTitle {
  margin: 0;
  text-align: none;
  font-family: "Quicksand", sans-serif;
  text-transform: uppercase;
  color: #999999;
  font-weight: 500;
  letter-spacing: 5px;
  margin-bottom: 15px;
  display: block;
  font-size: 1rem;
}


#projectsTitle {
  margin: 0;
  text-align: none;

  font-size: 1.2rem;
  margin-bottom: 4em;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 5px;
  line-height: 1.8;
  font-family: "Quicksand", sans-serif;
}

.hover-content {
    display:none;
}

.project:hover .hover-content {
  background: rgba(0, 0, 0, 0.7);
  position: absolute;
  width: 40rem;
  height: 30rem;
  text-align: left;
  //border-bottom-left-radius: 100rem;
  //border-bottom-right-radius: 100rem;
  display:block;


  -webkit-animation: fadein .5s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
        animation: fadein .5s;
}

.mobileProject:hover .hover-content {
  text-align: left;
  background: rgba(0, 0, 0, 0.7);
  position: absolute;
  width: 40rem;
  height: 30rem;
  //border-bottom-left-radius: 100rem;
  //border-bottom-right-radius: 100rem;
  display:block;


  -webkit-animation: fadein .5s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
        animation: fadein .5s;
}

.hover-content-title {
  padding: 1rem;
  padding-left: 2rem;
  padding-bottom: 0;
  font-family: "Quicksand", sans-serif;
  font-size: 1.5rem;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  width: 30rem;
}

.hover-content-type {
  padding-left: 2rem;
  font-family: "Quicksand", sans-serif;
  text-transform: uppercase;
  color: #999999;
  font-weight: 500;
  letter-spacing: 5px;
  margin-bottom: 15px;
}

.hover-content-paragraph {
  padding-left: 2rem;
  font-family: "Quicksand", sans-serif;
  color: #999999;
  font-weight: 500;
  letter-spacing: 1px;
  margin-bottom: 15px;
  width: 37rem;
}

.slider {
	overflow-y: hidden;
	max-height: 500px; /* approximate max height */

	-webkit-transition-property: all;

	-o-transition-property: all;

	transition-property: all;
	-webkit-transition-duration: .5s;
	     -o-transition-duration: .5s;
	        transition-duration: .5s;
	-webkit-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
	     -o-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
	        transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
}

.slider.closed {
	max-height: 0;
}


@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Firefox < 16 */

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Internet Explorer */

